body, html{
background: ;  /* fallback for old browsers */
}
.App{
margin: .7rem;
}
.loading{
display: flex;
justify-content: center;
align-items: center;
font-size: 38px;
color: black;
text-shadow: 2px 2px white;
}
.title {
color: black;
position: absolute;
text-align: center;
font-size: 20px;
font-weight: 700;
top: 50%;
padding: 5px 15px 5px 15px;
margin: 0px 2px 0px 2px;
text-shadow: 1px 1px white;
background-color: hsla(0,0%,90.2%,.6);
}
.grid-container, .grid-container-reverse {
display: grid;
position: relative;
grid-gap: 2px;
grid-template-columns: repeat(auto-fit, minmax(375px, 1fr));
grid-auto-rows: 400px;
grid-auto-flow: dense;
overflow: none;
}
.grid-container-reverse {
direction: rtl;
}
.subHeader{
margin-top: 25px;
}
article {
width: 100%;
height: 100%;
position: relative;
display: flex;
justify-content: center;
}

[class="0"] { grid-column: span 1; }
[class="1"] { grid-column: span 2;grid-row: span 1; }
[class="2"] { grid-column: span 2; grid-row: span 2; }
[class="3"] { grid-column: span 2;grid-row: span 1; }
[class="4"] { grid-column: span 1; }
[class="5"] { grid-column: span 2; }
[class="6"] { grid-column: span 1; }
[class="7"] { grid-column: span 2; }
[class="8"] { grid-column: span 1;grid-row: span 2; }
[class="9"] { grid-column: span 1; }
[class="10"] { grid-column: span 2; }
[class="11"] { grid-column: span 3; }
[class="12"] { grid-column: span 1;grid-row: span 1; }

.slider-container {
display: flex;
flex-direction: row;
position: relative;
width: 100%;
overflow: auto;
-webkit-overflow-scrolling: touch;
}
.carousel-wrapper {
margin: 10px;
-webkit-overflow-scrolling: touch;
}
.carousel-cell {
display: flex;
flex-direction: column-reverse;
align-items: baseline;
text-align: center;
-webkit-overflow-scrolling: touch;
}
.carousel-cell img {
width: 400px;
height: 25rem;
object-fit: cover;
position: relative;
-webkit-overflow-scrolling: touch;
}
/*==================Image overlay===========================*/
article .content-overlay {
background: rgba(0,0,0,0.4);
position: absolute;
height: 100%;
width: 100%;
left: 0;
top: 0;
bottom: 0;
right: 0;
opacity: 0;
-webkit-transition: all 0.1s ease-in-out 0s;
-moz-transition: all 0.1s ease-in-out 0s;
transition: all 0.1s ease-in-out 0s;
}

article:hover .content-overlay{
  opacity: 1;
}

.content-image{
  width: 100%;
}

.content-details {
  position: absolute;
  text-align: center;
  padding-left: 1em;
  padding-right: 1em;
  width: 100%;
  top: 50%;
  left: 50%;
  opacity: 0;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: all 0.2s ease-in-out 0s;
  -moz-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
}

article:hover .content-details{
  top: 50%;
  left: 50%;
  opacity: 1;
}

.content-details h3{
  color: #fff;
  font-weight: 500;
  letter-spacing: 0.15em;
  margin-bottom: 0.5em;
  text-transform: uppercase;
}

.content-details p{
  color: #fff;
  font-size: 0.8em;
}

.fadeIn-bottom{
  top: 80%;
}

/*==================End Image overlay===========================*/
.btnScroll {
padding: 0;
background: transparent;
border-style: none;
font-weight: 300;
font-size: 12px;
color: #848d95;
opacity: .9;
}
.btn-primary:hover{
-webkit-box-shadow: 0 1px 6px 0 rgba(32,33,36,0.28);
box-shadow: 0 1px 6px 0 rgba(32,33,36,0.28);
}
.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show>.btn-primary.dropdown-toggle:focus {
box-shadow: none;
}

.btnScroll:hover {
color: #007BFF;
}
button:focus{
outline: none;
}
.timeStamp{
position: absolute;
color: white;
align-self: flex-end;
font-size: 12px;
font-weight: 500;
text-shadow: 2px 2px black;
}
.timeStampS{
position: relative;
color: black;
margin-top: 12px;
font-weight: 300;
text-shadow: 2px 2px white;
font-size: 14px;
}
.stampContent {
display: flex;
flex-direction: column-reverse;
}
.smallText {
font-size: 16px;
}
p{
padding-top: 5px;
font-weight: 600;
}
.gridImg, .scrollImg {
width: 100%;
height: 100%;
object-fit: cover;
background-image: url(https://unfoxnews.com/upload/noimage.jpg);
}

.footer {
height: 5rem;
color: black;
display: -webkit-box;
display: -ms-flexbox;
display: flex;
-webkit-box-align: center;
-ms-flex-align: center;
align-items: center;
-webkit-box-pack: center;
-ms-flex-pack: center;
justify-content: center;
}
.fa-react {
color: #006BFF;
}